import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router'

@Component({
  templateUrl: './instance-info.component.html'
})
export class InstanceInfoComponent implements OnInit {

  frontCardText: string = 'Front Instance Info';
  backCardText: string = 'Back Instance Property';
  flipBackFlag: boolean = false;

  constructor(private _router: Router) { }

  ngOnInit() {
  }

  flip(){
    this.flipBackFlag = !this.flipBackFlag;
  }

  navigate(path: string){
    this._router.navigate([path]);
  }

}
