import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { LoginComponent } from './login/login.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ModuleToolComponent } from './module-tool/module-tool.component';
import { CspComponent } from './csp/csp.component';
import { CspLoginComponent } from './csp-login/csp-login.component';
import { InstanceInfoComponent } from './instance-info/instance-info.component';
import { UploaderComponent } from './uploader/uploader.component';
import { TestScenarioListComponent } from './test-scenario-list/test-scenario-list.component';
import { SummaryReportComponent } from './summary-report/summary-report.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { NotFoundComponent } from './not-found/not-found.component';

const routes: Routes = [
  {path: 'login', component: LoginComponent},
  {path: 'dashboard', component: DashboardComponent},
  {path: 'module-tool', component: ModuleToolComponent},
  {path: 'csp', component: CspComponent},
  {path: 'csp-login', component: CspLoginComponent},
  {path: 'instance-info', component: InstanceInfoComponent},
  {path: 'uploader', component: UploaderComponent},
  {path: 'test-scenario-list', component: TestScenarioListComponent},
  {path: 'summary-report', component: SummaryReportComponent},
  {path: '', redirectTo: '/login', pathMatch: 'full'},
  {path: '**', component: NotFoundComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
